.content {
  width: 750px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-top: 30px;

  border-radius: 2px;
  background-color: #f8f5de;
  background-image: linear-gradient(
    to right,
    rgba(255, 210, 0, 0.4),
    rgba(200, 160, 0, 0.1) 11%,
    rgba(0, 0, 0, 0) 35%,
    rgba(200, 160, 0, 0.1) 65%
  );
  box-shadow: inset 0 0 75px rgba(255, 210, 0, 0.3),
    inset 0 0 20px rgba(255, 210, 0, 0.4), inset 0 0 30px rgba(220, 120, 0, 0.8);
  color: rgba(0, 0, 0, 0.8);
  //width: calc(8.5in - 15em);
  letter-spacing: 0.05em;
  line-height: 1.6;
  padding: 5em 5em 5em 5em;
  @include for-tablet {
    margin: 15px;
  }
  @include for-mobile {
    margin: 15px;
  }
}

.modalList {
  list-style-type: none;
  padding: 0px;
  margin-top: 35px;
}

.modalList li {
  margin: 10px;
}

.close {
  background-color: transparent;
  color: #006e51;
  border: none;
  float: right;
  padding: 5px;
  border-radius: 35%;
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 15px;
  margin-right: 15px;
  font-size: 25px;
  transition: all 0.2s ease-in-out;
}

.close:hover,
.close:focus {
  background-color: transparent;
  color: #129471;
  text-decoration: none;
  cursor: pointer;
  transform: scale(1.1);
}

.info {
  width: 500px;
  //height: 500px;

  display: flex;
  flex-direction: column;
  //justify-content: center;
  align-items: center;
}

.info h2 {
  margin-bottom: 30px;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid black; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.15em; /* Adjust as needed */
  animation: typing 3.5s steps(30, end), blink-caret 0.5s step-end infinite;
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
}

.links {
  margin-top: 35px;
